import { useState } from "react";
import { CommsManager } from "jderobot-commsmanager";

import "./IdeInterface.css";
import {
  StatusBar,
  Explorer,
  CollapsableResizableColumn,
  ResizableColumn,
  ResizableRow,
  FileEditor,
} from "Components";
import { useTheme } from "Utils";
import {
  Entry,
  Layout,
  ExplorerEntry,
  EditorsEntry,
  ViewersEntry,
} from "Types";

import {
  StyledIdeContainer,
  StyledIdeHorizContainer,
  StyledIdeVertContainer,
  StyledViewerMenu,
} from "./IdeInterface.styles";

export interface IdeInterfaceStyles {
  bgColor?: string;
}

interface IdeInterfaceProps {
  commsManager: CommsManager | null;
  resetManager: Function;
  project: string;
  explorers: ExplorerEntry[];
  editorApi: any;
  extraEditors: EditorsEntry[];
  viewers: ViewersEntry[];
  layout: Layout;
  options: any;
}

const IdeInterface = ({
  commsManager,
  resetManager,
  project,
  explorers,
  editorApi,
  extraEditors,
  viewers,
  layout,
  options,
}: IdeInterfaceProps) => {
  const [currentFile, setCurrentFile] = useState<Entry | undefined>(undefined);
  const theme = useTheme();

  return (
    <StyledIdeHorizContainer bgColor={theme.palette?.primary}>
      <ResizableRow
        baseWidth={[20, 40, 40]}
        maxWidth={[40, 60, 60]}
        showExplorer={explorers.length > 0}
        layout={layout}
      >
        {explorers.length > 0 && (
          <ResizableColumn>
            {explorers.map((explorer) => (
              <Explorer
                setCurrentFile={setCurrentFile}
                currentFile={currentFile}
                project={project}
                api={explorer}
              />
            ))}
          </ResizableColumn>
        )}
        <StyledIdeVertContainer bgColor={theme.palette?.primary}>
          <StyledIdeContainer bgColor={theme.palette?.primary}>
            <FileEditor
              currentFile={currentFile}
              changeCurrentFile={setCurrentFile}
              currentProjectname={project}
              isUnibotics={false}
              autosave={true}
              manager={commsManager}
              api={editorApi}
              extraEditors={extraEditors}
            />
          </StyledIdeContainer>
        </StyledIdeVertContainer>
        <StyledIdeVertContainer bgColor={theme.palette?.primary}>
          <StyledIdeContainer bgColor={theme.palette?.primary}>
            <ViewersContainer viewers={viewers} options={options} />
          </StyledIdeContainer>
        </StyledIdeVertContainer>
      </ResizableRow>
      <StatusBar
        project={project}
        commsManager={commsManager}
        resetManager={resetManager}
      />
    </StyledIdeHorizContainer>
  );
};

export default IdeInterface;

const ViewersContainer = ({
  viewers,
  options,
}: {
  viewers: ViewersEntry[];
  options: any;
}) => {
  const [visibility, setVisibility] = useState<boolean[]>(
    Array(viewers.length).fill(false)
  );
  const theme = useTheme();

  const toggleVisibility = (index: number) => {
    setVisibility(
      visibility.map((state, i) => {
        if (index === i) {
          return !state;
        } else {
          return state;
        }
      })
    );
  };

  return (
    <>
      <StyledViewerMenu bgColor={theme.palette?.primary}>
        <div className="ide-viewer-buttons">
          {viewers.map((viewer, index) => (
            <button
              className={`ide-viewer-toggle-button ${
                visibility[index] ? "active" : ""
              }`}
              onClick={() => toggleVisibility(index)}
            >
              {viewer.icon}
            </button>
          ))}
        </div>
      </StyledViewerMenu>
      <CollapsableResizableColumn state={visibility}>
        {viewers.map((viewer) => viewer.component)}
      </CollapsableResizableColumn>
    </>
  );
};
