___CSS_LOADER_EXPORT___.push([module.id, `/* FileBrowser.css */
.bt-file-item-container {
  padding-left: 5px;
  padding-right: 5px;

  &:hover {
    background-color: var(--buttons-hover);
  }
}

.bt-file-item-container:hover {
  background-color: var(--buttons-hover);
}

.bt-file-item-selected-container {
  background-color: var(--buttons);

  &:hover {
    background-color: var(--buttons-hover);
  }
}

.bt-file-item {
  list-style-type: none;
  padding: 0px;
  cursor: pointer;
  display: flex;
  flex-direction: row;
  align-items: center;
  height: 20px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  position: relative;
  z-index: 1;
}

.bt-file-item label {
  cursor: pointer;
  list-style-type: none;
  padding: 3px;
  font-size: 0.9em;
  color: var(--text);
}

.bt-accent-color {
  position: relative;
  z-index: 10;
  width: 5px;
  height: 100%;
}

.bt-more-action-icon {
  margin-left: auto;
  visibility: hidden;
}

.bt-file-item-container:hover .bt-more-action-icon {
  visibility: visible;
}

.bt-arrow-icon {
  height: 16px;
  width: 16px;
  min-width: 16px;
  min-height: 16px;
}
