___CSS_LOADER_EXPORT___.push([module.id, `.bt-modal::backdrop {
  background: hsla(0, 0%, 0%, 0.5);
}

.bt-modal {
  /* max-width: 20rem; */
  width: 500px;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
  border: 2px solid var(--buttons);
  border-radius: 0.5rem;
  position: relative;
  box-shadow: hsl(0 0% 0% / 10%) 0 0 0.5rem 0.25rem;
  background: var(--background);
}

.bt-modal-contents form,
.bt-modal-contents {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  flex-grow: 1;
}

.bt-modal:focus {
  outline: none;
}

.bt-modal-titlebar {
  color: var(--text);
  font-size: large;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  grid-column-gap: 5px;
  justify-items: center;
}

.bt-modal-titlebar-close {
  margin-left: auto;
  height: 100%;
  border-radius: 10px;
  background-color: var(--buttons);
}

.bt-modal-titlebar-back {
  margin-right: auto;
  height: 100%;
  border-radius: 10px;
  background-color: var(--buttons);
}

.bt-modal-titlebar-close:hover,
.bt-modal-titlebar-back:hover {
  background-color: var(--buttons-hover);
}

.bt-modal-titlebar-title {
  font-weight: 600;
  align-self: center;
  grid-column-start: 2;
}

/* Modal Input with changing placeholder text */

.bt-modal-complex-input-row-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.bt-modal-complex-input-container {
  position: relative;
}

.bt-modal-complex-input {
  border: 2px solid var(--border-color);
  background-color: var(--input-background) !important;
  border-radius: 10px;
  padding-left: 1em;
  padding-top: 13px;
  width: 100%;
  align-self: center;
  height: 50px;
  color: var(--text);
  outline: 0;
  font-size: 1rem;
  transition: border-color 0.2s;
}

.bt-modal-complex-input:focus {
  border-color: var(--input-focus-border);
  outline: none !important;
}

.bt-modal-complex-input::placeholder {
  color: transparent;
  user-select: none;
}

.bt-modal-complex-input:placeholder-shown ~ .bt-modal-complex-input-label {
  font-size: 1rem;
  cursor: text;
  top: 15px;
}

.bt-modal-complex-input:not(placeholder-shown) ~ .bt-modal-complex-input-label {
  position: absolute;
  top: 5px;
  display: block;
  transition: 0.2s;
  font-size: 0.9rem;
  font-weight: 700;
  cursor: text;
}

.bt-modal-complex-input:focus ~ .bt-modal-complex-input-label {
  position: absolute;
  top: 5px;
  display: block;
  transition: 0.2s;
  font-size: 0.9rem;
  font-weight: 700;
}

/* reset input */
.bt-modal-complex-input:required,
.bt-modal-complex-input:invalid {
  box-shadow: none;
}

.bt-modal-complex-input-label {
  position: absolute;
  top: 15px;
  left: 17px;
  display: block;
  transition: 0.2s;
  font-size: 1rem;
  color: var(--input-placeholder-text);
  user-select: none;
}

.bt-modal-complex-input-invalid {
  border-color: var(--error-border) !important;
}

.bt-modal-complex-input-indications {
  color: var(--input-placeholder-text);
  font-size: 0.7rem;
}

/* End of complex input */
