___CSS_LOADER_EXPORT___.push([module.id, `.bt-more-actions-menu {
  position: absolute;
  width: auto;
  z-index: 100;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;

  border: 2px solid var(--buttons);
  border-radius: 0.5rem;
  box-shadow: hsl(0 0% 0% / 10%) 0 0 0.5rem 0.25rem;
  background: var(--background);
}

.bt-more-actions-menu-entry {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.2rem;
  padding-bottom: 0.2rem;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
}

.bt-more-actions-menu-divider {
  height: 2px;
  margin-top: 0.4rem;
  margin-bottom: 0.4rem;
  width: 100%;
  background: var(--buttons);
}

.bt-more-actions-menu-entry:first-of-type {
  margin-top: 0.4rem;
}

.bt-more-actions-menu-entry:last-of-type {
  margin-bottom: 0.4rem;
}

.bt-more-actions-menu-entry:hover {
  background: var(--buttons);
}
