___CSS_LOADER_EXPORT___.push([module.id, `.bt-sidebar-content {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  flex: 1 1 1px;
}

.bt-sidebar-entry {
  height: fit-content;
  width: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  gap: 4px;
}

.bt-sidebar-entry-contents {
  overflow-y: auto;
}

.bt-sidebar-entry-menu {
  display: flex;
  align-items: center;
  height: 32px;
  padding: 0 10px 0 10px;
  gap: 4px;
  background-color: var(--control-bar);
}

.bt-force-other-side {
  margin-left: auto;
}

.bt-sidebar-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  background-color: var(--control-bar);
  border: 0;
  padding: 0 0 0 0;
  &:hover {
    background-color: var(--buttons-hover);
    border-radius: 5px;
  }
}

.bt-menu-button {
  display: flex;
  justify-content: center;
  width: 20px;
  height: 20px;
  background-color: var(--buttons-invisible);
  border: 0;
  margin-left: 3px;
  margin-right: 3px;
  padding: 0 0 0 0;
  align-content: center;
  flex-wrap: wrap;
}

.bt-menu-button:hover {
  background-color: var(--buttons-hover);
  border-radius: 5px;
}

.bt-icon {
  width: 20px;
  height: 20px;
}
